ter;\n  padding: 0.6em;\n\n  > .ui-icon.size-8{\n    margin-left: 0.6em;\n  }\n}",".ui-color-picker{\n\n  $slider-height: 10px;\n  $slider-margin: 30px 0;\n  $handle-margin-top: -14px;\n  $layer-border: none; //solid 1px #808080;\n  $layer-border-radius: 5px;\n  $swatch-height: 40px;\n\n  %handle{\n    width: 8px;\n    height: 8px;\n    border: solid 1px silver;\n    background: white;\n  }\n\n  %handleAura{\n    content: '.';\n    color: transparent;\n    width: 40px;\n    height: 40px;\n    background: rgba(127, 127, 127, 0.2);\n    position: absolute;\n    border-radius: 100%;\n    top: -16px;\n    left: -16px;\n  }\n\n  .swatch{\n    display: block;\n    height: 20px;\n  }\n\n  > .layer{\n    margin: 10px 0;\n\n    &:first-child{ margin-top: 0; }\n    &:last-child{ margin-bottom: 0; }\n\n    &.swatch{\n\n      border-radius: $layer-border-radius;\n      overflow: hidden;\n      border: $layer-border;\n      height: $swatch-height;\n      .swatch{\n        height: $swatch-height;\n      }\n    }\n\n    &.slider-2d{\n      .ui-range{\n        height: 100px;\n        background-size: 100% 100px;\n        background-repeat: no-repeat;\n        border-radius: $layer-border-radius;\n        border: $layer-border;\n        .handle{\n          @extend %handle;\n          &:before{ @extend %handleAura; }\n        }\n      }\n    }\n\n    &.slider-1d{\n      .ui-range{\n        border: $layer-border;\n        border-radius: $layer-border-radius;\n        height: $slider-height;\n        margin: $slider-margin;\n        .handle{\n          @extend %handle;\n          &:before{ @extend %handleAura; }\n        }\n      }\n    }\n\n    &.slider-alpha{\n      .ui-range{\n        border: $layer-border;\n        height: $slider-height;\n        margin: $slider-margin;\n        background-color: transparent;\n        background-size: contain;\n        background-repeat: repeat;\n        .handle{\n          @extend %handle;\n          &:after{ @extend %handleAura; }\n        }\n        &:before{\n          content: '.'; color: transparent;\n          position: absolute;\n          left: 0; top: 0; right: 0; bottom: 0;\n          background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);\n          border-radius: $layer-border-radius;\n        }\n      }\n    }\n    &.inputs{\n      display: flex;\n      .item{\n        flex-shrink: 3;\n        text-align: center;\n\n        &.hex{\n          flex-shrink: 1;\n        }\n        .label{\n          margin: 10px 0;\n        }\n\n        input{\n          width: 70%;\n          text-align: center;\n        }\n      }\n    }\n  }\n\n}",".ui-clickable{\n\n  &:not(.disabled){\n    cursor: pointer;\n  }\n\n  &.disabled{\n    opacity: 0.3;\n  }\n\n  &.is-open,&:hover{\n\n  }\n}",".ui-icon {\n  background: center center no-repeat;\n  background-size: 100% 100%;\n\n  &.size-8 { width: 8px;  height: 8px;  }\n  &.size-16{ width: 16px; height: 16px; }\n  &.size-20{ width: 20px; height: 20px; }\n  &.size-32{ width: 32px; height: 32px; }\n  &.size-50{ width: 50px; height: 50px; }\n\n}\n",".ui-item-group{\n  display: inline-flex;\n  overflow: hidden;\n  align-items: stretch;\n}",".ui-label{\n\n  &.with-icon{\n    display: flex;\n    align-items: center;\n\n    .ui-icon{\n      margin-right: 0.5em;\n    }\n  }\n\n  .desc{\n    opacity: 50%;\n  }\n}\n",".ui-separator{\n  position: relative;\n  height: 1em;\n\n  &::after{\n    display: block;\n    position: absolute;\n    content: '.';\n    color: transparent;\n    left: 0;\n    right: 0;\n    top: 50%;\n    height: 1px;\n  }\n}",".ui-textbox {\n  display: inline-flex;\n  position: relative;\n  border: solid 1px WindowFrame;\n  background-color: white;\n\n  input{\n    border: none;\n    background-color: white;\n    &:focus{\n      outline: none;\n    }\n  }\n}",".ui-menuitem{\n  position: relative;\n  display: flex;\n  align-items: center;\n  padding: 0.6em;\n  cursor: pointer;\n\n\n  &.is-open,&:hover{\n    /* Colors */\n  }\n\n  .ui-icon.icon-chevron-right{\n    position: absolute;\n    right: 0.6em;\n  }\n\n  .ui-label{\n    padding-right: 25px; // Based on a 16px icon to be positioned as chevron to the right\n  }\n}",".ui-tree-item{\n  display: flex;\n  padding: 0.6em;\n  align-items: center;\n  cursor: pointer;\n\n  > .collapse-glyph {\n    margin-right: 0.6em;\n  }\n}\n\n.tree-item-children{\n}",".ui-range{\n\n  height: 40px;\n  border-radius: 20px;\n  background: #f0f0f0;\n  background-size: contain;\n  position: relative;\n\n  .handle{\n    position: absolute;\n    width: 40px;\n    height: 40px;\n    border-radius: 100%;\n    background: Highlight;\n  }\n\n}",".ui-dock-view{\n\n  &, > .dock-side, > .main-side{\n    position: absolute;\n    top:0;\n    left:0;\n    right: 0;\n    bottom:0;\n  }\n\n  .dock-side, > .main-side {\n    overflow: auto;\n  }\n\n  &.side-left   > .dock-side{ right:  auto; }\n  &.side-top    > .dock-side{ bottom: auto; }\n  &.side-right  > .dock-side{ left:   auto; }\n  &.side-bottom > .dock-side{ top:    auto; }\n}",".ui-split-view{\n\n  &.side-left > .dock-side{   border-right-style: solid; border-right-width: 1px; }\n  &.side-top > .dock-side{    border-bottom-style: solid; border-bottom-width: 1px; }\n  &.side-right > .dock-side{  border-left-style: solid; border-left-width: 1px; }\n  &.side-bottom > .dock-side{ border-top-style: solid; border-top-width: 1px; }\n}",".ui-tab-view{\n  > .tabs{\n    > .items > .tab{\n      padding-left: 2em;\n      padding-right: 2em;\n    }\n  }\n\n}",".ui-tree-view{\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n}",".ui-toolbar-view{\n\n  > .toolbar{\n    display: flex;\n    justify-content: space-between;\n\n    > .items{\n      display: flex;\n      align-items: stretch;\n    }\n  }\n\n  > .content{\n    flex-grow: 1;\n    overflow: auto;\n  }\n\n  &.side-left, &.side-right{\n    > .toolbar{\n      flex-direction: column;\n\n      > .items{\n        flex-direction: column;\n      }\n    }\n  }\n\n}",".ui-menu-overlay {\n  min-width: 100px;\n  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);\n  z-index: 10000;\n}",".ui-suggestions-overlay{\n  background-color: Menu;\n\n  .ui-clickable, .ui-loader{\n    padding: 0.6em;\n  }\n}",".ui-overlay{\n  position: fixed;\n  z-index: 10000;\n}","\n$button-face-hover-bg: Highlight !default;\n$button-face-hover-fg: HighlightText !default;\n$button-face-selected-bg: Highlight !default;\n$button-face-selected-fg: HighlightText !default;\n$window-bg: Window !default;\n$window-fg: WindowText !default;\n$window-border: #ddd !default;\n$separator: rgba(0, 0, 0, 0.13) !default;\n\n.ui-clickable.is-open{\n    background-color: $button-face-hover-bg;\n    color: $button-face-hover-fg;\n}\n\n.ui-button-wrap.split:hover > .ui-button-split-part,\n.ui-button-wrap.split:hover > .ui-button,\n.ui-button:hover:not(.face-never):not(.disabled),\n.ui-clickable.selected:not(.disabled),\n.ui-button.is-open{\n    background-color: $button-face-hover-bg;\n    color: $button-face-hover-fg;\n}\n\n.ui-menuitem.is-open,\n.ui-menuitem.is-selected{\n    background-color: $button-face-selected-bg;\n    color: $button-face-selected-fg;\n}\n\n.ui-menu-overlay,\n.ui-suggestions-overlay{\n    background-color: $window-bg;\n    color: $window-fg;\n    border: solid 1px $window-border;\n    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);\n}\n\n.ui-separator:after{\n    background-color: $separator;\n}\n.ui-item-group{\n    border: solid 1px $window-border;\n    border-radius: 0.5em;\n}\n\n.ui-item-group > .ui-clickable.selected:not(.disabled){\n    background-color: #ddd;\n}\n\n.ui-split-view > .dock-side{ border-color: $window-border;  }\n\n.ui-tab-view > .tabs { background-color: #ddd;}\n.ui-tab-view > .tabs  > .items > .tab {margin-top: 0.6em;}\n.ui-tab-view > .tabs  > .items > .selected{ background-color: white; }\n\n.ui-tree-view:focus .ui-tree-item.selected{\n    background-color: Highlight;\n    color: HighlightText;\n}\n\n.ui-tree-item.selected{\n    background-color: #f0f0f0;\n    color: HighlightText;\n}\n\n.ui-tree-item:not(.selected):hover{\n    background-color: #E3EFFE;\n}"],"sourceRoot":""}]);
